import React from "react";

import { useStaticQuery, graphql } from "gatsby";
import {GatsbyImage, getImage} from "gatsby-plugin-image";

const PetitsAnimauxBanner = (props) => {
  const data = useStaticQuery(graphql`
    query CoverPhoto {
      allGooglePhotosPhoto(filter: {description: {eq: "shop_cover_banner" } }) {
        nodes {
          id
          file {
            childImageSharp {
              gatsbyImageData(
                placeholder: BLURRED
                formats: [AUTO, WEBP]
              )
            }
          }
          description
        }
      }
    }
  `)

  const {allGooglePhotosPhoto} = data;
  const coverImg = allGooglePhotosPhoto.nodes[0];

  return (
    <GatsbyImage
      image={getImage(coverImg.file)}
      alt="Crochet Nest Banner"
    />
  );
}

export default PetitsAnimauxBanner;