import React from "react";
import { H3 } from "../Header";
import Link from "../Link";
import StripeBuyButton from "../StripeBuyButton";

import { GatsbyImage, getImage } from "gatsby-plugin-image";

import "./styles.scss";

const PetitsAnimauxProductCard = (props) => {
    const { node } = props;
    return (
        <div className="PetitsAnimauxCardContainer">
            <div className="PetitsAnimauxCard">
                <Link className="PetitsAnimauxCardLink" href={`/product/${node.id}`} useTheming={false}>
                    <div className="PetitsAnimauxCardImage">
                        <GatsbyImage
                            image={getImage(node.coverPhoto.file)}
                            alt={node.name}
                        />
                    </div>
                    <H3 className="PetitsAnimauxCardTitle">{node.name}</H3>

                    {node.dimensions.map((str, idx) => {
                        return <p className="PetitsAnimauxDescription" key={idx}>{str}</p>
                    })}

                </Link>
                <StripeBuyButton stripeUrl={node.stripeUrl}> USD ${node.price}</StripeBuyButton>
            </div>
        </div>
    );
}

export default PetitsAnimauxProductCard;